body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*shared css styles*/
@font-face {
  font-family: lovelo;
  src: url(/static/media/Lovelo_Black.fca6bef8.otf);
}

.page {
  padding-top: 50px;
  padding-bottom: 20px;
}

body {
  background-color: #deeeef;
  color: #4d5154;
}

.lovelo {
  font-family: lovelo;
}

i.icon {
  margin: 0;
  height: auto;
}

.navbar-brand {
  font-family: lovelo;
  line-height: 100%;
}

.navbar-light .navbar-nav .nav-link {
  color: grey;
}

.pageTitle {
  background-color: #aab3ba;
  padding: 20px;
  color: white;
  margin-bottom: 20px;
  box-shadow: 0 .125rem .25rem rgba(0,0,0,.075)!important;
}

.pageTitle h1 {
  font-weight: bold;
}

.teal {
  color: teal;
}

.red {
  color: red;
}

.ui.form .field>label {
  color: #4d5154;
}

.row {
  -webkit-justify-content: center;
          justify-content: center;
}

.savingChangesMessage {
  position: fixed !important;
  bottom: 0;
  width: 50%;
  margin: 0 25% 25px 25% !important;
}

#landing .story, #story .sentence {
  border: 1px solid lightgrey;
  border-radius: 5px;
  margin-bottom: 10px;
  background-color: white;
  cursor: pointer;
}

#landing .story:hover, #story .sentence:hover  {
  border-color: #b3b2b2;
  box-shadow: 0 .125rem .25rem rgba(0,0,0,.075)!important;
}

#landing .story, #story .sentence {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
  cursor: pointer;
}

#landing .story .content, #story .sentence .content  {
  padding: 10px 0 10px 0;
}


#landing .story .content .storyTitle, #story .story .content .storyTitle {
  font-weight: bold;
}

#landing .story .upvotes, #story .upvotes {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
  margin-right: 10px;
  padding: 10px 0 10px 0;
  width: 5%;
  border-radius: 5px 0 0 5px;
  background-color: #f5f5f5;
  font-weight: bold;
  -webkit-justify-content: center;
          justify-content: center;
}

#landing .story .upvotes .upvoteButton, #story .upvotes .upvoteButton {
  color: #7e7b7b;
  border:none;
  background: none;
}

#landing .story .upvotes .up:hover, #story .upvotes .up:hover {
  color: #7bd4ad;
}


#landing .story .upvotes .down:hover, #story .upvotes .down:hover {
  color: #ff9393;
}

#landing .story .upvotes .upvoted, #story .upvotes .upvoted {
  color: #7bd4ad;
}

#landing .story .upvotes .downvoted, #story .upvotes .downvoted {
  color: #ff9393;
}

#landing .story .content, #story .content {
  width: 95%
}

#landing .story .content .metadata, #story .content .metadata {
  color: grey;
  font-size: 0.8rem;
}

#landing .story .content .metadata .author, #story .content .metadata .author  {
  cursor: pointer;
  font-weight: bold;
  text-decoration: none;
  color: grey;
}

#landing .story .content .metadata .author:hover, #story .content .metadata .author:hover {
  color: #4d5154;
}

#landing .story .content .metadata .inprogress, #story .content .metadata .inprogress  {
  color: #ff9393;
}

header a,.act-as-a {
    cursor: pointer;
}

header a:hover {
    text-decoration: none;
}


header .user a {
    color: #4d5154 !important;
}

header .profilePic {
    background-color: #f5f5f5;
    margin: 0 10px 0 0;
    border-radius: 5px;
    width: 30px;
    height: 30px;
}

